        { 城市: '高密', 销售额: 6257.44 },
        { 城市: '高州', 销售额: 2156.7 },
        { 城市: '高邮', 销售额: 13141.856 },
        { 城市: '鱼洞', 销售额: 288.96 },
        { 城市: '鸡东', 销售额: 27367.9 },
        { 城市: '鸡西', 销售额: 36601.18 },
        { 城市: '鹤壁', 销售额: 24516.212 },
        { 城市: '鹤岗', 销售额: 32579.064 },
        { 城市: '鹿城', 销售额: 18225.62 },
        { 城市: '麻城', 销售额: 1187.172 },
        { 城市: '黄山', 销售额: 8189.72 },
        { 城市: '黄岩', 销售额: 3981.376 },
        { 城市: '黄州', 销售额: 2425.36 },
        { 城市: '黄梅', 销售额: 9153.06 },
        { 城市: '黄石', 销售额: 33450.648 },
        { 城市: '黄陂', 销售额: 1821.876 },
        { 城市: '黎城', 销售额: 8857.38 },
        { 城市: '黑山', 销售额: 819.84 },
        { 城市: '齐齐哈尔', 销售额: 65754.815 },
        { 城市: '龙井', 销售额: 355.04 },
        { 城市: '龙凤', 销售额: 2549.064 },
        { 城市: '龙口', 销售额: 10317.02 },
        { 城市: '龙江', 销售额: 1302.28 },
        { 城市: '龙泉', 销售额: 54789.14 }
      ],
      dataPrice: [
        { action: '浏览网站', pv: 50000 },
        { action: '放入购物车', pv: 35000 },
        { action: '生成订单', pv: 25000 },
        { action: '支付订单', pv: 15000 },
        { action: '完成交易', pv: 8500 }
      ]
    }
  },
  mounted () {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = this.data
    const bar = new Bar('container', {
      data,
      xField: 'value',
      yField: 'year',
      seriesField: 'year',
      legend: {
        position: 'top-left'
      }
    })

    bar.render()

    this.containerGs()
    this.containerGz()
  },
  methods: {
    containerGs () {
      const data = this.datalist
      const column = new Bar('containerGs', {
        data,
        yField: '城市',
        xField: '销售额',
        yAxis: {
          label: {
            autoRotate: false
          }
        },
        scrollbar: {
          type: 'vertical'
        }
      })

      column.render()
    },
    containerGz () {
      const data = this.dataPrice
      const barPlot = new Bar('containerGz', {
        data,
        xField: 'pv',
        yField: 'action',
        conversionTag: {}
      })

      barPlot.render()
    }
  },
  created () {
    // var wrapperElement = '<div style="position:relative;"></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo{
  display flex
  flex-flow wrap
  .box{
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display inline-block
    padding 0 20px 20px
    margin 20px
    h3{
      line-height 50px
      height 50px
      text-align center
    }
  }
  #container{
    display block
    width 500px
    height 300px
  }
  #containerGs{
    display block
    width 500px
    height 300px
  }
  #containerGz{
    display block
    width 500px
    height 300px
  }
}
</style>



